<script>
  import { afterUpdate } from 'svelte';
  export let length;
  export let repeats = [];
  let elements = [];

  $: repeatRows = [[]];
  $: repeatTimesPerRow = [];

  $: selectedRepeat = undefined;
  $: amountOfNumberInputs = [];
  $: {
    amountOfNumberInputs = [];
    for (let i = 0; i < repeatRows.length; i++) {
      let ids = getIds(i);
      amountOfNumberInputs[i] = [];
      ids.forEach((id) => {
        amountOfNumberInputs[i].push(id);
      });
    }
    amountOfNumberInputs = [...amountOfNumberInputs];
  }

  $: {
    repeats = [];
    for (let i = 0; i < repeatRows.length; i++) {
      let rep = getRepeats(repeatRows[i], repeatTimesPerRow[i]);
      if (rep.length > 0) {
        repeats[i] = rep;
      }
    }
  }

  let activeElement;
  export function insertColumn(index) {
    for (let i = 0; i < repeatRows.length; i++) {
      if (index < repeatRows.length - 1) {
        repeatRows[i].splice(index, 0, repeatRows[i][index]);
      } else {
        repeatRows[i].push(0);
      }
    }
    repeatRows = [...repeatRows];
  }

  export function removeColumn(index) {
    for (let i = 0; i < repeatRows.length; i++) {
      repeatRows[i].splice(index, 1);
      postChangeCheck(repeatRows[i]);
    }
    repeatRows = [...repeatRows];
  }

  let distanceBetweenPoints;
  let fromRow;
  let fromColumn;
  let startX;
  let startClientX;
  let elementActive = false;
  function dragStart(event, row, column) {
    console.log('dragStart');
    //elementActive = false;
    fromRow = row;
    fromColumn = column;
    startX = event.target.getBoundingClientRect().x;
    startClientX = event.clientX;

    document.body.classList.add('dragging');
    document.addEventListener('mousemove', dragMove);
    document.addEventListener('pointerup', dragEnd, { once: true });
  }

  //let clickFromRow;
  //let clickFromColumn;
  let lastX;
  let lastFromRow;
  let lastFromColumn;
  function dragEnd(event) {
    let delta = event.clientX - startClientX;
    if (delta === 0) {
      //TODO: Set active element here for mouse clicking action (non-dragging)
      if (elementActive) {
        startX = lastX;
        fromRow = lastFromRow;
        fromColumn = lastFromColumn;
        dragMove(event);
        elementActive = false;
      } else {
        lastX = startX;
        lastFromRow = fromRow;
        lastFromColumn = fromColumn;
        elementActive = true;
      }
    }
    document.body.classList.remove('dragging');
    document.removeEventListener('mousemove', dragMove);
  }

  function dragMove(event) {
    let x = event.clientX;

    let row = repeatRows[fromRow];
    let delta = Math.round((x - startX) / distanceBetweenPoints);
    console.log('between ', startX, 'and', x);
    console.log('delta', delta);
    if (
      delta === 0 ||
      fromColumn + delta < 0 ||
      fromColumn + delta > length - 2
    ) {
      return;
    }

    let id = row[fromColumn];
    if (delta !== 0) {
      while (
        delta !== 0 &&
        encompassUnitInRowAbove(id, fromColumn, delta, fromRow)
      ) {
        let change = Math.sign(delta);
        id = modifyRepeat(id, fromRow, change, fromColumn);
        fromColumn += change;
        startX += distanceBetweenPoints * change;
        delta -= change;
      }
      postChangeCheck();
      repeatRows[fromRow] = row;
      repeatRows = [...repeatRows];
    }
  }

  function isEndpoint(position, rowNum) {
    let row = repeatRows[rowNum];
    let id = row[position];
    if (
      row[position - 1] !== id ||
      row[position + 1] !== id ||
      position === 0 ||
      position === row.length - 1
    ) {
      return true;
    }
    return false;
  }

  function encompassUnitInRowAbove(id, fromColumn, delta, fromRow) {
    if (fromRow === 0) {
      return true;
    }

    let row = repeatRows[fromRow - 1];
    let currentRow = repeatRows[fromRow];
    let repeats = getRepeats(row);
    let target = fromColumn + delta;

    if (target === 0) {
      return true;
    }
    if (row[target] === 0 || target === row.length - 1) {
      return true;
    }
    if (isEndpoint(target, fromRow) && currentRow[target] === id) {
      return true;
    }
    for (let rep of repeats) {
      if (currentRow[target] !== id || id === 0) {
        if (delta > 0 && target === rep.to) {
          return true;
        }
        if (delta < 0 && target === rep.from) {
          return true;
        }
      } else {
        if (delta > 0 && target === rep.from) {
          return true;
        }
        if (delta < 0 && target === rep.to) {
          return true;
        }
      }
    }
    return false;
  }

  function getRepeats(row, repeatTimes = []) {
    let lastValue = -1;
    let repeats = [];
    for (let i = 0; i < row.length; i++) {
      if (lastValue !== row[i]) {
        if (repeats.length > 0) {
          repeats[repeats.length - 1].to = i - 1;
        }
        repeats.push({ from: i, times: repeatTimes[row[i]], id: row[i] });
        lastValue = row[i];
      }
    }
    if (repeats.length > 0 && repeats[repeats.length - 1].to === undefined) {
      repeats[repeats.length - 1].to = row.length - 1;
    }
    return repeats.filter((r) => r.id !== 0);
  }

  function getIds(row) {
    return new Set(repeatRows[row].filter((a) => a !== 0));
  }

  function generateId(row) {
    return repeatRows[row].reduce((a, b) => Math.max(a, b)) + 1;
  }

  function modifyRepeat(id, rowNumber, delta, position) {
    let row = repeatRows[rowNumber];
    if (id === 0) {
      id = generateId(fromRow);
      row[position] = id;

      if (repeatTimesPerRow[fromRow] === undefined) {
        repeatTimesPerRow[fromRow] = [];
      }
      repeatTimesPerRow[fromRow][id] = 1;
    }
    if (row[position + delta] !== id) {
      row[position + delta] = id;
      for (let i = rowNumber + 1; i < repeatRows.length; i++) {
        let subRow = repeatRows[i];
        if (subRow[position] !== subRow[position + delta]) {
          subRow[position + delta] = subRow[position];
        }
      }
    } else {
      row[position] = 0;
      for (let i = rowNumber + 1; i < repeatRows.length; i++) {
        let subRow = repeatRows[i];
        if (subRow[position] !== subRow[position - delta]) {
          subRow[position] = 0;
        }
      }
    }

    return id;
  }

  function postChangeCheck() {
    for (let row of repeatRows) {
      // Remove 1-length repeats
      for (let i = 1; i < row.length - 1; i++) {
        let pre = row[i - 1];
        let id = row[i];
        let post = row[i + 1];
        if (pre !== id && post !== id) {
          row[i] = 0;
        }
      }
      //Remove 1-length from both ends
      if (row[0] !== row[1]) {
        row[0] = 0;
      }
      if (row[row.length - 1] !== row[row.length - 2]) {
        row[row.length - 1] = 0;
      }
    }
    let lastRow = repeatRows[repeatRows.length - 1];
    //If last row has repeats, add row with zeroes
    if (lastRow.some((v) => v !== 0)) {
      repeatRows.push(Array(length - 1).fill(0));
      repeatTimesPerRow.push([]);
      //If two last rows are all zeroes, remove one.
    }
    if (repeatRows.length > 1) {
      let secondToLastRow = repeatRows[repeatRows.length - 2];
      if (
        (lastRow.every((v) => v === 0) &&
          secondToLastRow.every((v) => v === 0)) ||
        (secondToLastRow[0] !== 0 &&
          secondToLastRow.every((v) => v === secondToLastRow[0]))
      ) {
        repeatRows.length = repeatRows.length - 1;
      }
    }
  }

  function getPositionOfNumberInput(id, row) {
    let elms = elements[row];
    let buttons = elms.querySelectorAll('button');
    let first;
    let last;
    for (let i = 0; i < buttons.length; i++) {
      let item = Number(buttons[i].getAttribute('data-id'));
      if (first === undefined && item === id) {
        first = i;
      }
      if (first !== undefined && item !== id) {
        last = i - 1;
        break;
      }
    }
    if (
      last === undefined &&
      Number(buttons[buttons.length - 1].getAttribute('data-id')) === id
    ) {
      last = buttons.length - 1;
    }
    let firstRect = buttons[first].getBoundingClientRect();
    let lastRect = buttons[last].getBoundingClientRect();
    let parentRect = elms.getBoundingClientRect();
    return [
      (firstRect.x + lastRect.x) / 2.0 - parentRect.x - firstRect.width / 2,
      (firstRect.y + lastRect.y) / 2.0 - parentRect.y - 2,
    ];
  }

  afterUpdate(() => {
    elements
      .filter((e) => e !== null)
      .forEach((element) => {
        let inputs = element.querySelectorAll('.times-input');
        inputs.forEach((elm) => {
          let pos = getPositionOfNumberInput(
            Number(elm.getAttribute('data-id')),
            Number(elm.getAttribute('data-row'))
          );
          elm.style.left = pos[0] + 'px';
          elm.style.top = pos[1] + 'px';
        });
      });
    if (
      distanceBetweenPoints === undefined &&
      elements !== undefined &&
      elements[0] !== undefined
    ) {
      let buttons = elements[0].querySelectorAll('button');
      if (buttons[0] !== undefined && buttons[1] !== undefined) {
        let a = buttons[0].getBoundingClientRect();
        let b = buttons[1].getBoundingClientRect();
        distanceBetweenPoints = b.x - a.x;
      }
    }
  });

  function setRepeatTimes(id, row, value) {
    repeatTimesPerRow[row][id] = value;
    repeatTimesPerRow = [...repeatTimesPerRow];
  }
</script>

{#if length > 2}
  {#each repeatRows as repeatRow, row}
    <tr class="repeat" bind:this={elements[row]}>
      {#each { length } as _, column}
        <td>
          <div class="repeat-container">
            {#if column !== 0}
              <div
                class="line left"
                class:visible={repeatRows[row][column - 1] !== 0 &&
                  repeatRows[row][column - 2] !== 0 &&
                  repeatRows[row][column - 1] === repeatRows[row][column - 2]}
              />
              {#if repeatRows[row][column - 2] !== repeatRows[row][column - 1] || repeatRows[row][column] !== repeatRows[row][column - 1] || repeatRows[row][column - 1] === 0 || (selectedRepeat !== undefined && (repeatRows[row][column - 1] === 0 || selectedRepeat.id === repeatRows[row][column - 1]))}
                <button
                  class:active={fromColumn === column - 1 &&
                    fromRow === row &&
                    elementActive}
                  data-id={repeatRows[row][column - 1]}
                  data-row={row}
                  on:pointerdown={(event) => dragStart(event, row, column - 1)}
                />
              {/if}
              <div
                class="line right"
                class:visible={repeatRows[row][column - 1] !== 0 &&
                  repeatRows[row][column] !== 0 &&
                  repeatRows[row][column - 1] === repeatRows[row][column]}
              />
            {/if}
          </div>
        </td>
      {/each}
      <div>
        {#each amountOfNumberInputs[row] as id}
          <input
            type="text"
            class="times-input"
            on:input={(e) => setRepeatTimes(id, row, Number(e.target.value))}
            data-id={id}
            data-row={row}
            value={repeatTimesPerRow[row][id]}
          />
        {/each}
      </div>
    </tr>
  {/each}
{/if}

<style>
  .repeat td {
    padding: 0;
  }

  .repeat {
    position: relative;
  }

  .repeat-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 4px;
    margin-bottom: 8px;
  }

  .repeat-container .line {
    flex-grow: 1;
    background-color: black;
    height: 4px;
    visibility: hidden;
  }

  .repeat-container .line.visible {
    visibility: visible;
  }

  .repeat .repeat-container button {
    margin: 0;
    padding: 0;
    width: 15px;
    height: 15px;
    border-radius: 100%;
    border: 4px solid black;
    background-color: black;
  }

  .repeat .repeat-container button:hover,
  .repeat .repeat-container button.active {
    background-color: white;
    cursor: -webkit-grab;
    cursor: grab;
  }

  .repeat,
  .repeat td {
    border: 0;
  }

  .times-input {
    position: absolute;
    width: 2em;
    margin: 0;
    padding: 0;
    text-align: center;
    background-color: white;
    border: 1px solid black;
  }
</style>
