<script>
  import Repeats from './Repeats';
  import draft from 'stores/Draft';
  import YarnDropDown from './YarnDropDown';

  $: length = 1;
  $: if (data[0] !== undefined && data[0].length > 0) {
    length = data[0].length;
  }

  let repeats = [];

  $: yarn = -1;
  $: data = [];
  $: if (yarn !== -1) {
    data = [...data, [yarn, ...Array(getLength() - 1)]];
    yarn = -1;
  }

  function getTableThreads() {
    let threads = [];
    if (data !== undefined && data.length > 0) {
      for (let i = 1; i < data[0].length; i++) {
        for (let j = 0; j < data.length; j++) {
          if (data[j][i] !== undefined && threads[i - 1] === undefined) {
            threads[i - 1] = { yarn: data[j][0], times: data[j][i] };
          }
        }
      }
    }
    return threads;
  }

  function getLength() {
    return length;
  }

  let repeatComponent;

  function deleteRow(row) {
    data = data.filter((v, i) => i !== row);
  }

  function setValue(column, row, value) {
    for (let i = 0; i < data.length; i++) {
      data[i][column] = undefined;
    }
    data[row][column] = value;
    data = [...data];
  }

  function insertColumn(index) {
    repeatComponent.insertColumn(index - 1);
    data.forEach((row) => row.splice(index, 0, undefined));
    data = [...data];
  }

  export function getThreads() {
    let tableThreads = getTableThreads();
    let threads = [...tableThreads];
    for (let i = 0; i < repeats.length; i++) {
      let repeatRow = repeats[i];
      for (let j = repeatRow.length - 1; j >= 0; j--) {
        let rep = repeatRow[j];
        let part = threads.slice(rep.from, rep.to + 1);
        for (let k = 0; k < rep.times - 1; k++) {
          threads.splice(rep.from, 0, ...part);
        }
        let change = (rep.to - rep.from + 1) * (rep.times - 1);
        // Adjust lower repeats after changed thread list length
        for (let k = i + 1; k < repeats.length; k++) {
          let lowerRow = repeats[k];
          for (let l = 0; l < lowerRow.length; l++) {
            let rep1 = lowerRow[l];
            if (rep1.to >= rep.to) {
              rep1.to += change;
            }
          }
        }
      }
    }
    let ret = [];
    threads.forEach((th) => {
      for (let i = 0; i < th.times; i++) {
        ret.push(th.yarn);
      }
    });
    ret.reverse();
    return ret;
  }

  function onFocus(columnIndex) {
    if (lastBlur !== undefined && columnIndex !== lastBlur) {
      let column = lastBlur;
      let removeColumn = true;
      for (let i = 0; i < data.length; i++) {
        if (
          data[i][column] !== undefined &&
          data[i][column] !== '' &&
          data[i][column] !== 0
        ) {
          removeColumn = false;
          break;
        }
      }
      if (removeColumn) {
        data.forEach((row) => row.splice(column, 1));
        repeatComponent.removeColumn(column - 1);
        data = [...data];
      }
    }
  }

  let lastBlur;
  function onBlur(columnIndex) {
    lastBlur = columnIndex;
  }
</script>

<div class="top">
  <table>
    <tbody>
      {#if data.length > 0}
        <tr class="button-row">
          {#each { length: data[0].length } as _, column}
            <td>
              <button
                class="add-button"
                on:click={() => insertColumn(column + 1)}>↓</button
              >
            </td>
          {/each}
        </tr>
      {/if}
      {#each data as _, row}
        <tr>
          <td>
            <YarnDropDown
              selectedYarn={data[row][0]}
              on:input={(e) => {
                if (e.target.value === '-1') {
                  deleteRow(row);
                } else {
                  data[row][0] = e.target.value;
                }
              }}
            />
          </td>
          {#each { length: data[row].length - 1 } as _, column}
            <td>
              <input
                size="4"
                on:focus={() => onFocus(column + 1)}
                on:blur={() => onBlur(column + 1)}
                value={data[row][column + 1] !== undefined
                  ? data[row][column + 1]
                  : ''}
                on:input={(e) =>
                  setValue(column + 1, row, Number(e.target.value))}
              />
            </td>
          {/each}
        </tr>
      {/each}
      <tr>
        <td>
          <YarnDropDown bind:selectedYarn={yarn} />
        </td>
      </tr>

      <Repeats bind:this={repeatComponent} bind:length bind:repeats />
    </tbody>
  </table>
</div>

<style>
  table {
    border-collapse: collapse;
    border: 3px solid black;
    background-color: white;
    position: relative;
  }

  .top {
    padding-top: 20px;
  }
  td {
    border: 3px solid black;
    min-width: 1.5rem;
    text-align: center;
  }

  tr {
    height: 1rem;
  }

  input {
    margin: 0;
    padding: 0;
    width: 3em;
    background: none;
    border-radius: 0;
  }

  .button-row,
  .button-row td {
    border: 0;
    height: 0;
    white-space: nowrap;
    line-height: 0px;
  }

  .add-button {
    width: 20px;
    height: 20px;
    margin: 0;
    padding: 0;
    float: right;
    transform: translate(calc(50% + 2px), calc(-100% - 4px));
  }
</style>
