<style>
  fieldset {
    margin-bottom: 10px;
    border: none;
  }

  fieldset > *:not(legend) {
    margin-left: 12px;
  }

  legend {
    font-size: 1.5rem;
  }

  label {
    margin-right: 10px;
    display: flex;
    align-items: center;
  }

  .to-from-inputs input {
    margin: 5px 0;
    width: 5rem;
  }

  .repeat-options {
    display: grid;
    grid-template-columns: 1fr;
  }

  .repeat-options > div {
    display: flex;
    justify-content: space-between;
  }
</style>
