<script>
  import { onMount, afterUpdate } from 'svelte';
  import ui from '../../stores/UI';
  import YarnSelector from '../../components/YarnSelector/YarnSelector.svelte';

  export let height = 0;
  export let scrollContainer;
  export let sidebarOpen;
  $: cellSizeInput = $ui.cellSize;
  let toolbarContainer;
  let hasChangedCellSize;
  let scrollPercentage = [];

  $: {
    console.log(sidebarOpen);
  }

  onMount(() => {
    height = toolbarContainer.clientHeight;
  });

  afterUpdate(() => {
    if (hasChangedCellSize) {
      hasChangedCellSize = false;
      let [scrollLeftMax, scrollTopMax] = getBounds();
      let x = (1.0 - scrollPercentage[0]) * scrollLeftMax;
      let y = (1.0 - scrollPercentage[1]) * scrollTopMax;
      scrollContainer.scrollTo(x, y);
    }
  });

  function getBounds() {
    let scrollLeftMax =
      scrollContainer.scrollWidth - scrollContainer.clientWidth;
    let scrollTopMax =
      scrollContainer.scrollHeight - scrollContainer.clientHeight;
    return [scrollLeftMax, scrollTopMax];
  }

  function toggleTreadleOrder() {
    ui.update((draft) => {
      draft.showTreadleOrder = !draft.showTreadleOrder;
    });
  }

  function toggleThreading() {
    ui.update((draft) => {
      draft.showThreading = !draft.showThreading;
    });
  }

  function toggle() {
    sidebarOpen = !sidebarOpen;
    if (sidebarOpen === false) {
      ui.update((temp) => {
        temp.selectedMenu = -1;
      });
    }
  }
</script>

<div class="tool-bar" bind:this={toolbarContainer}>
  <button class="hamberder" on:click={toggle}
    >{#if !sidebarOpen}≡{:else}x{/if}</button
  >

  <button on:click={toggleThreading}>Toggle threading</button>
  <button on:click={toggleTreadleOrder}>Toggle treadle order</button>
  <YarnSelector />
</div>

<style>
  .tool-bar {
    justify-content: space-between;
    display: flex;
    background-color: var(--color-1);
    pointer-events: all;

    grid-column: span 2;
    z-index: 10;
  }

  .hamberder {
    height: 100%;
    min-width: 2em;
  }
</style>
