<script>
  import { onMount } from 'svelte';

  import HelpIcon from 'icons/help.svg';
  import BackIcon from 'icons/back.svg';
  import ScarfIcon from 'icons/scarf.svg';
  import YarnIcon from 'icons/yarn.svg';
  import PatternBucket from 'icons/pattern-bucket.svg';
  import Repeat from 'icons/repeat.svg';

  import ui from 'stores/UI';
  import draft from 'stores/Draft';
  import WeaveSettings from '../Sidebar/pages/WeaveSettings.svelte';
  import YarnSettings from '../Sidebar/pages/YarnSettings.svelte';
  import RepeatComponent from '../Sidebar/pages/Repeat.svelte';
  import PatternFill from '../Sidebar/pages/PatternFill/PatternFill.svelte';
  import ColorPickerDialog from 'pages/Editor/ColorPickerDialog/ColorPickerDialog';

  export let sidebarWidth;
  export let toolbarHeight;
  let dialogElement;
  $: selectedMenu = $ui.selectedMenu;
  let items = [
    {
      icon: ScarfIcon,
      title: 'Weave settings',
      component: WeaveSettings,
    },
    {
      icon: YarnIcon,
      title: 'Yarns',
      component: YarnSettings,
    },
    {
      icon: PatternBucket,
      title: 'Fill with pattern',
      component: PatternFill,
    },
    {
      icon: Repeat,
      title: 'Repeat',
      component: RepeatComponent,
    },
  ];

  onMount(() => {
    document.body.addEventListener('pointerdown', onClick);
  });

  function onClick(event) {
    if (
      selectedMenu !== -1 &&
      !dialogElement.contains(event.target) &&
      ColorPickerDialog.element !== undefined &&
      !ColorPickerDialog.element.contains(event.target)
    ) {
      console.log('close event preventDefault');
      event.preventDefault();
      ui.update((temp) => {
        temp.selectedMenu = -1;
      });
      document.body.removeEventListener('pointerdown', onClick);
    }
  }
</script>

<div class="dialog" style={`top: ${toolbarHeight}px`} bind:this={dialogElement}>
  <svelte:component this={items[selectedMenu].component} />
</div>

<style>
  @media (min-width: 900px) {
    .dialog {
      width: 30vw;
      min-width: 30em;
    }
  }

  @media (max-width: 900px) {
    .dialog {
      top: 0 !important;
    }
  }

  .dialog {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    overflow: auto;

    z-index: 10;
    background-color: var(--color-5);
    padding: 20px;
    border: 1px solid var(--color-4);
  }
</style>
